* {
  outline: none; 
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/pattern.ae9becc3.png)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f11e; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 225, 0, 0.733); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(216, 191, 0, 0.788); 
}
@font-face {
  font-family: 'starwars';
  src: url(/static/media/STJEDISE.a43e30ec.TTF);
}

h1 {
  font-family: 'starwars';
  color: rgb(255, 225, 0);
}

.card {
  background-color: rgba(43, 43, 43, 0.466);
  color: #eee;
}


